																this.get_redemption_records_redemption_date();
														this.get_message_feedback_user_name();
										this.get_message_feedback_message_date();
								this.get_shipping_notice_shipment_number();
										this.get_shipping_notice_product_name();
																				this.get_return_and_exchange_application_shipment_number();
										this.get_return_and_exchange_application_product_name();
							this.get_return_and_exchange_application_operation_date();
																	this.get_sales_information_product_type();
							this.get_sales_information_sales_date();
						  },
  watch: {
	$route() {
	  $.push(this.query, this.$route.query);
	  this.get_goods();
				  this.get_ordinary_users_user_name();
						  this.get_ordinary_users_user_gender();
							  this.get_product_information_commodity_specifications();
																	  this.get_point_products_product_name();
						  this.get_point_products_product_type();
																			  this.get_product_type_product_type();
							  this.get_redemption_records_exchange_number();
									  this.get_redemption_records_product_name();
															  this.get_redemption_records_redemption_date();
													  this.get_message_feedback_user_name();
									  this.get_message_feedback_message_date();
							  this.get_shipping_notice_shipment_number();
									  this.get_shipping_notice_product_name();
																			  this.get_return_and_exchange_application_shipment_number();
									  this.get_return_and_exchange_application_product_name();
